"use client";
import React, { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { useUser } from "../app/context/UserContext";
import { motion, AnimatePresence } from "framer-motion";

const Navbar = ({ className }: { className?: string }) => {
  const [open, setOpen] = useState(false);
  const { user, setUser } = useUser();
  const router = useRouter();

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const res = await fetch("/api/auth/me");
        if (res.ok) {
          const data = await res.json();
          setUser(data.user || null);
        } else setUser(null);
      } catch {
        setUser(null);
      }
    };
    fetchUser();
  }, [setUser]);

  const handleScroll = (id: string) => {
    if (id.startsWith("/")) router.push(id);
    else {
      const el = document.getElementById(id);
      if (el) {
        const yOffset = -100;
        const y = el.getBoundingClientRect().top + window.pageYOffset + yOffset;
        window.scrollTo({ top: y, behavior: "smooth" });
      }
    }
    setOpen(false);
  };

  const handleLogout = async () => {
    await fetch("/api/auth/logout", { method: "POST" });
    setUser(null);
    router.push("/");
  };

  return (
    <div className={`relative w-full flex justify-center ${className || ""}`}>
      <motion.nav
        initial={{ y: -100, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ type: "spring", stiffness: 120, damping: 20 }}
        className="fixed top-6 w-full max-w-7xl mx-auto flex items-center justify-between px-12 py-4 z-50 rounded-[2rem] backdrop-blur-xl border border-gray-100/10 bg-white/5 shadow-[0_8px_32px_rgba(0,0,0,0.15)]"
      >
        {/* Logo */}
        <motion.div
          onClick={() => handleScroll("/")}
          className="flex items-center gap-4 cursor-pointer"
          whileHover={{ scale: 1.1, rotate: [0, 5, -5, 0] }}
          transition={{ duration: 0.6 }}
        >
          <img
            src="https://cdn-icons-png.flaticon.com/128/892/892930.png"
            alt="CleanTM Logo"
            className="h-10 w-10"
          />
          <span className="text-2xl font-extrabold text-gray-900">CleanTM</span>
        </motion.div>

        {/* Desktop Links */}
        <ul className="hidden lg:flex items-center gap-14 font-semibold text-gray-700 text-lg">
          {[
            { label: "Home", id: "/" },
            { label: "Map", id: "map" },
            { label: "Weekly Quests", id: "/task" },
            { label: "Rewards", id: "/shop" },
          ].map((i) => (
            <motion.li
              key={i.label}
              onClick={() => handleScroll(i.id)}
              className="relative cursor-pointer"
              whileHover={{ y: -3 }}
              transition={{ type: "spring", stiffness: 200, damping: 10 }}
            >
              <span className="hover:text-green-600 transition-colors duration-300">
                {i.label}
              </span>
              <motion.span
                layoutId="underline"
                className="absolute left-0 -bottom-1 h-1 bg-green-500 rounded-full"
              />
            </motion.li>
          ))}
        </ul>

        {/* User CTA */}
        <div className="hidden lg:flex items-center gap-4">
          {user ? (
            <>
              <span className="font-semibold text-gray-700">{user.email}</span>
              <motion.button
                onClick={handleLogout}
                className="px-6 py-2 rounded-full bg-red-600 text-white font-bold shadow-lg"
                whileHover={{ scale: 1.05 }}
                transition={{ type: "spring", stiffness: 300 }}
              >
                Logout
              </motion.button>
            </>
          ) : (
            <motion.button
              onClick={() => handleScroll("/api/login")}
              className="px-6 py-2 rounded-full bg-green-600 text-white font-bold shadow-lg"
              whileHover={{ scale: 1.05 }}
              transition={{ type: "spring", stiffness: 300 }}
            >
              Login
            </motion.button>
          )}
        </div>

        {/* Mobile Menu Button */}
        <button
          onClick={() => setOpen(!open)}
          className="lg:hidden relative w-10 h-10 flex flex-col justify-between"
        >
          <span
            className={`block h-1 w-full bg-gray-900 rounded transition-transform duration-300 ${
              open ? "rotate-45 translate-y-4" : ""
            }`}
          ></span>
          <span
            className={`block h-1 w-full bg-gray-900 rounded transition-opacity duration-300 ${
              open ? "opacity-0" : ""
            }`}
          ></span>
          <span
            className={`block h-1 w-full bg-gray-900 rounded transition-transform duration-300 ${
              open ? "-rotate-45 -translate-y-4" : ""
            }`}
          ></span>
        </button>
      </motion.nav>

      {/* Mobile Dropdown */}
      <AnimatePresence>
        {open && (
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className="lg:hidden fixed top-[6.5rem] w-full max-w-7xl mx-auto px-8 py-6 rounded-2xl bg-white/95 backdrop-blur-lg shadow-lg border border-gray-200/10"
          >
            <ul className="flex flex-col gap-6 text-lg font-medium text-gray-700">
              <li
                onClick={() => handleScroll("/")}
                className="hover:text-green-600 cursor-pointer"
              >
                Home
              </li>
              <li
                onClick={() => handleScroll("map")}
                className="hover:text-green-600 cursor-pointer"
              >
                Map
              </li>
              <li
                onClick={() => handleScroll("/task")}
                className="hover:text-green-600 cursor-pointer"
              >
                Weekly Quests
              </li>
              <li
                onClick={() => handleScroll("/shop")}
                className="hover:text-green-600 cursor-pointer"
              >
                Rewards
              </li>
            </ul>

            <div className="mt-6 flex flex-col gap-3">
              {user ? (
                <>
                  <span className="text-gray-700 font-semibold">
                    {user.email}
                  </span>
                  <motion.button
                    onClick={handleLogout}
                    className="w-full py-3 rounded-full bg-red-600 text-white font-bold shadow-md"
                    whileHover={{ scale: 1.05 }}
                  >
                    Logout
                  </motion.button>
                </>
              ) : (
                <motion.button
                  onClick={() => handleScroll("/api/login")}
                  className="w-full py-3 rounded-full bg-green-600 text-white font-bold shadow-md"
                  whileHover={{ scale: 1.05 }}
                >
                  Login
                </motion.button>
              )}
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default Navbar;
